import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';

import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import DatePicker                                   from 'material-ui/DatePicker';
import Divider                                      from 'material-ui/Divider';
import TextField                                    from 'material-ui/TextField';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import MuiThemeProvider                             from 'material-ui/styles/MuiThemeProvider';
import FlatPagination                               from 'material-ui-flat-pagination';

import moment                                       from 'moment';
/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionBroadcastMessageList, { ReduxEnumBroadcastMessageList } from '../../../_redux/action/reduxAction.broadcast.message.list';




/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';
import { ObjectUtil }                            	from '../../../_util/util.object';



/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';



/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';

import '../s.broadcast.message.css';

export default connect(
    ( state ) => {

        let thisState = state[ 'ReduxBroadcastMessageList' ] ;
        let stateObj = { };

        if ( thisState ) {

            for ( let i = 0 ; i < thisState.length; i ++ ) {
                let recordItem = thisState[ i ];
                if ( !recordItem[ 'completed' ] ) {

                    if ( ReduxEnumBroadcastMessageList.INIT_DONE == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    } else if ( ReduxEnumBroadcastMessageList.LIST_DONE == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestListDone';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    }


                    recordItem[ 'completed' ] = true;
                }
            }

        }

        return stateObj;
    }
) (
    class ComponentBroadcastMessageList extends ComponentAbstract {
        constructor( props ) {
            super( props )

            console.log( 'ComponentBroadcastMessageList : constructor : *** : [' + this.uuid() + '] ' , props );

            this.state = {
                isBegin           : true

                , isLoading       : false

                , result          : []

                , form : {
                    title: '',
                    startDate: '',
                    formattedStartDate: '',
                    endDate: '',
                    formattedEndDate: '',
                    module: '',
                    mode: ''
                }
                
                , pagination : {
                    offset : 1
                    , limit : 20
                    , total : 100
                }

                , MODULE : [
                    { id: 1 , name: "All"  }
                    , { id: 2 , name: "Home"  }
                    , { id: 3 , name: "Login"  }
                ]

                , MODE : [
                    { id: 1 , name: "Web"  }
                    , { id: 2 , name: "Mobile"  }
                ]
            }

        }


        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    listener
            ################################################################################################################################################
            ################################################################################################################################################
        */

       _onReady() {
            console.log( 'ComponentBroadcastMessageList : _onReady : *** ');

        }

        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    event
            ################################################################################################################################################
            ################################################################################################################################################
        */

        _handleChange( key , value ) {
            let form = this.state.form;
            form[ key ] = value;
            this.setState( {
                form : form
            } )
        }

        onClickClear() {
            let form = this.state.form;
            form.title='',
            form.startDate='',
            form.formattedStartDate='',
            form.formattedEndDate='',
            form.endDate='',
            form.module='',
            form.mode=''
            this.setState({
                form : form
            })
        }

        onClickSearch() {
            console.log( 'ComponentBroadcastMessageList : onClickSearch : *** ' );
            this.requestList();
        }

        onClickDetail( status, statusHeader, item ) {
            console.log( 'ComponentBroadcastMessageList : onClickDetail : *** ' );
            HelperNavigate.route(
                HelperNavigate.mapPath( HelperNavigateENUM.BROADCAST_MESSAGE_DETAIL )
                , {module: this.state.MODULE, mode: this.state.MODE 
                    , status : status, statusHeader : statusHeader
                    , item :  item
                }
            );
        }

        getStartDate() {
            if (this.state.form.startDate) {
                return new Date(this.state.form.startDate);
            }
        }

        getEndDate() {
            if (this.state.form.endDate) {
                return new Date(this.state.form.endDate);
            }
        }

        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    request
            ################################################################################################################################################
            ################################################################################################################################################
        */
       requestInitDone( result ) {
            console.log( 'ComponentBroadcastMessageList : requestInitDone : *** : result => ', result );

            this.setState( {
                isLoading: false
            } );
            this.dispatch(
                ReduxActionBroadcastMessageList.rRequestEnd()
            );
        }

        requestList( ) {
            console.log( 'ComponentBroadcastMessageList : requestList : ***' )

            Promise.all( [
                ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
            ] ).then(
                ( value ) => {

                    this.setState(
                        { isLoading: true }
                        , () => {
                            this.dispatch(
                                ReduxActionBroadcastMessageList.rList(
                                    { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                    , {
                                        title: this.state.form.title,
                                        startDate: this.state.form.formattedStartDate,
                                        endDate: this.state.form.formattedEndDate,
                                        module: this.state.form.module,
                                        mode: this.state.form.mode
                                    }
                                )
                            )
                        }
                    );

                }
            )

        }

        requestListDone( result ) {
            console.log( 'ComponentBroadcastMessageList : requestListDone : *** : result => ', result );

            let pagination = this.state['pagination'];
            pagination['total'] = result['broadcastMessages'].length / pagination['limit'];

            this.setState( {
                isLoading: false
                , result : result['broadcastMessages']
                , pagination : pagination
            } );
            this.dispatch(
                ReduxActionBroadcastMessageList.rRequestEnd()
            );
        }

        /*
            ################################################################################################################################################
            ################################################################################################################################################
                    render
            ################################################################################################################################################
            ################################################################################################################################################
        */

        render() {
            return (
                    <div>
                        
                        <h4 style={ { paddingLeft: 25 } }>Broadcast Message Listing</h4>

                        <div style={ { paddingLeft: 25, paddingBottom: 10  } }>

                            <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                <div style={ { flex: 0.2 } }>

                                    <p style={ { paddingTop: 12 } }>Search By</p>

                                </div>

                                <div style={ { flex: 0.2 } }>
                                </div>

                            </div>

                            <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                <div style={ { flex: 0.1 } }>

                                    <p style={ { paddingTop: 12 } }>Title</p>

                                </div>

                                <div style={ { flex: 0.3 } }>

                                    <TextField 
                                        name={ 'list_title' }
                                        underlineStyle={ { borderColor: '#000' } }
                                        underlineFocusStyle={ { borderColor: '#FF5252' } }
                                        style={ { width : '50%' } } 
                                        value={ this.state.form.title }
                                        onChange={ ( event ) => {
                                            this._handleChange( 'title' , event.target.value );
                                        } } />

                                </div>

                            </div>

                            <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                <div style={ { flex: 0.1 } }>

                                    <p style={ { paddingTop: 12 } }>Start Date</p>

                                </div>

                                <div style={ { flex: 0.3 } }>

                                    <DatePicker
                                        name={ 'list_startDate' }
                                        style={ { flex: 1 } }
                                        hintText="Select Start Date"
                                        value={ this.getStartDate() }
                                        onChange={ ( event, date ) => {
                                            this._handleChange( 'startDate' , date );
                                            this._handleChange( 'formattedStartDate' , (moment(date).format('YYYYMMDD') + moment().format('HHmmssSSS')) );
                                        } } /> 

                                </div>

                            </div>

                            <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                <div style={ { flex: 0.1 } }>

                                    <p style={ { paddingTop: 12 } }>End Date</p>

                                </div>

                                <div style={ { flex: 0.3 } }>

                                    <DatePicker
                                        name={ 'list_endDate' }
                                        style={ { flex: 1 } }
                                        hintText="Select End Date"
                                        value={ this.getEndDate() }
                                        onChange={ ( event, date ) => {
                                            this._handleChange( 'endDate' , date );
                                            this._handleChange( 'formattedEndDate' , (moment(date).format('YYYYMMDD') + moment().format('HHmmssSSS')) );
                                        } } /> 

                                </div>

                            </div>

                            <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                <div style={ { flex: 0.1 } }>

                                    <p style={ { paddingTop: 12 } }>Module</p>

                                </div>

                                <div style={ { flex: 0.3 } }>

                                    <SelectField 
                                        style={{ flex: 1 }}
                                        name={ 'list_module' }
                                        underlineStyle={ { borderColor: '#000' } }
                                        underlineFocusStyle={ { borderColor: '#FF5252' } }
                                        style={{ width : '50%' }}
                                        floatingLabelText="Select Module"
                                        value = { this.state.form.module }
                                        onChange={(event, index, value) => {
                                            this._handleChange('module', value);
                                        }}>
                                        <MenuItem value={ this.state.MODULE[0]['id'] } primaryText={ this.state.MODULE[0]['name'] } />
                                        <MenuItem value={ this.state.MODULE[1]['id'] } primaryText={ this.state.MODULE[1]['name'] } />
                                        <MenuItem value={ this.state.MODULE[2]['id'] } primaryText={ this.state.MODULE[2]['name'] } />
                                    </SelectField>

                                </div>

                            </div>

                            <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                <div style={ { flex: 0.1 } }>

                                    <p style={ { paddingTop: 12 } }>Mode</p>

                                </div>

                                <div style={ { flex: 0.3 } }>

                                    <SelectField 
                                        style={{ flex: 1 }}
                                        name={ 'list_mode' }
                                        underlineStyle={ { borderColor: '#000' } }
                                        underlineFocusStyle={ { borderColor: '#FF5252' } }
                                        style={{ width : '50%' }}
                                        floatingLabelText="Select Mode"
                                        value = { this.state.form.mode }
                                        onChange={(event, index, value) => {
                                            this._handleChange('mode', value);
                                        }}>
                                        <MenuItem value={ this.state.MODE[0]['id'] } primaryText={ this.state.MODE[0]['name'] } />
                                        <MenuItem value={ this.state.MODE[1]['id'] } primaryText={ this.state.MODE[1]['name'] } />
                                    </SelectField>

                                </div>

                            </div>

                            <div style={ { display: 'flex' , justifyContent: 'flex-start', marginTop: 10 } }>
                                <div style={ { flex: 0.14 } }/>
                                <RaisedButton
                                                label={ "Clear" }
                                                onClick={ () => {
                                                    this.onClickClear();
                                                } }
                                    />
                                    <div style={ { flex: 0.02 } }/>
                                <RaisedButton
                                                label={ "Search" }
                                                onClick={ () => {
                                                    this.onClickSearch();
                                                } }
                                    />

                            </div>

                            <hr/>

                            <div style={ { marginTop : '40px' } } >

                                <div style={ { display: 'flex' , justifyContent: 'center' } }>
                                    
                                    <MuiThemeProvider>
                                        <FlatPagination
                                        offset={ this.state.pagination.offset }
                                        limit={ this.state.pagination.limit }
                                        total={ (this.state.result.length > 0) ? this.state.pagination.total : 0 }
                                        nextPageLabel={ 'Next' }
                                        previousPageLabel={ 'Previous' }
                                        otherPageLabelStyle={ {
                                            fontSize : '16px'
                                        } }
                                        currentPageLabelStyle={ {
                                            fontSize : '16px'
                                        } }
                                        onClick={ (e, offset) => { 
                                            let pagination = this.state.pagination;
                                            pagination[ 'offset' ] = offset;
                                            this.setState( { pagination : pagination } )
                                        } }
                                        />
                                    </MuiThemeProvider>


                                </div>

                                <div style={ { marginTop : '10px' } }>

                                    <Table>
                                        <TableHeader displaySelectAll={ false }>
                                            <TableRow displayBorder={ true }>
                                                <TableHeaderColumn style={{backgroundColor:'#dc3545', color: 'white', textAlign: 'center'}}>No.</TableHeaderColumn>
                                                <TableHeaderColumn style={{backgroundColor:'#dc3545', color: 'white', textAlign: 'center'}}>Title</TableHeaderColumn>
                                                <TableHeaderColumn style={{backgroundColor:'#dc3545', color: 'white', textAlign: 'center'}}>From Date/Time</TableHeaderColumn>
                                                <TableHeaderColumn style={{backgroundColor:'#dc3545', color: 'white', textAlign: 'center'}}>To Date/Time</TableHeaderColumn>
                                                <TableHeaderColumn style={{backgroundColor:'#dc3545', color: 'white', textAlign: 'center'}}>Module</TableHeaderColumn>
                                                <TableHeaderColumn style={{backgroundColor:'#dc3545', color: 'white', textAlign: 'center'}}>Mode</TableHeaderColumn>
                                            </TableRow>
                                        </TableHeader>
                                        <TableBody displayRowCheckbox={ false }>
                                            {
                                                this.state.result.map( ( item, key ) => {
                                                    return (
                                                        <TableRow key={ item.bmId } displayBorder={ true }>
                                                            <TableRowColumn style={{textAlign: 'center'}}> { key + 1 } </TableRowColumn>
                                                            <TableRowColumn>
                                                                <FlatButton
                                                                        label={ item.title }
                                                                        primary={ true }
                                                                        linkButton={ true }
                                                                        onClick={ () => {
                                                                            this.onClickDetail( 'view', 'View Broadcast Message', item )
                                                                        } }
                                                                    />
                                                            </TableRowColumn>
                                                            <TableRowColumn> { moment(item.startDate.substr(0, 8)+'T'+item.startDate.substr(8, 6)).format('YYYY-MM-DD HH:mm:ss') } </TableRowColumn>
                                                            <TableRowColumn> { moment(item.endDate.substr(0, 8)+'T'+item.endDate.substr(8, 6)).format('YYYY-MM-DD HH:mm:ss') } </TableRowColumn>
                                                            <TableRowColumn> { this.state.MODULE.find((obj) => {return obj.id === (+item.module)}).name } </TableRowColumn>
                                                            <TableRowColumn> { this.state.MODE.find((obj) => {return obj.id === (+item.mode)}).name } </TableRowColumn>
                                                        </TableRow> 
                                                    );                                            
                                                })
                                            }
                                        </TableBody>
                                    </Table>

                                </div>
                                <br/>
                                <div style={{textAlign: 'center'}} >

                                    
                                    <RaisedButton
                                                        className={ 'broadcast-message-margin-10' }
                                                        label={ "Add" }
                                                        onClick={ () => {
                                                            this.onClickDetail('add', 'Add Broadcast Message');
                                                        } } />

                                   
                                </div>

                            </div>

                        </div>

                             <ComponentCustomLoading
                        visible={ this.state.isLoading }
                    />
                    
                    </div>
                )
            }
    }

)


